.primary-nav__screen--off {
	visibility: hidden;
	opacity: 0;
	transition: visibility 0s 0.3s, opacity 0.3s linear;
}

.primary-nav__screen--on {
	visibility: visible;
	opacity: 0.8;
	transition: opacity 0.3s linear;
}

.primary-nav__trigger {
	display: none;
	@media(max-width: $screen-sm-max) {
		display: inline-block;
	}
	span.menu-text {
		padding-right: 1rem;
	}
	.trigger__icon {
		width: 1.6rem;
		height: 1.2rem;
		margin-top: 0.6rem;
		position: relative;
		float: right;
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
		-webkit-transition: .5s ease-in-out;
		-moz-transition: .5s ease-in-out;
		-o-transition: .5s ease-in-out;
		transition: .5s ease-in-out;
		cursor: pointer;
		span {
			display: block;
			position: absolute;
			height: 1px;
			width: 100%;
			background: $white;
			opacity: 1;
			left: 0;
			-webkit-transform: rotate(0deg);
			-moz-transform: rotate(0deg);
			-o-transform: rotate(0deg);
			transform: rotate(0deg);
			-webkit-transition: .25s ease-in-out;
			-moz-transition: .25s ease-in-out;
			-o-transition: .25s ease-in-out;
			transition: .25s ease-in-out;
		}
		span:nth-child(1) {
			top: 0;
		}
		span:nth-child(2),
		span:nth-child(3) {
			top: 0.6rem;
		}
		span:nth-child(4) {
			top: 1.2rem;
		}
	}
	.trigger__icon.trigger--x span:nth-child(1) {
		top: 1.2rem;
		width: 0%;
		left: 50%;
	}
	.trigger__icon.trigger--x span:nth-child(2) {
		-webkit-transform: rotate(45deg);
		-moz-transform: rotate(45deg);
		-o-transform: rotate(45deg);
		transform: rotate(45deg);
	}
	.trigger__icon.trigger--x span:nth-child(3) {
		-webkit-transform: rotate(-45deg);
		-moz-transform: rotate(-45deg);
		-o-transform: rotate(-45deg);
		transform: rotate(-45deg);
	}
	.trigger__icon.trigger--x span:nth-child(4) {
		top: 1rem;
		width: 0%;
		left: 50%;
	}
}

.primary-nav {
	height: 7rem;
	li {
		width: 10%;
		display: table-cell;
		vertical-align: middle;
	}
	.primary-nav__logo {
		background-color: transparent;
		width: 20%;
		overflow: hidden;
		position: relative;
		svg {
			// SVG has a ratio of 600 x 180 or 10:3
			$logo_svg_width: 26;
			$logo_svg_height: 7.8;
			$logo_svg_half_width: #{$logo_svg_width/2};
			$logo_svg_half_height: #{$logo_svg_height/2};
			height: #{$logo_svg_height+rem};
			width: #{$logo_svg_width+rem};
			padding: 0;
			opacity: 0.8;
			margin: 0;
			margin-top: -#{$logo_svg_half_height+rem};
			margin-left: -#{$logo_svg_half_width+rem};
			position: absolute;
			top: 50%;
			left: 50%;
			@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
				margin-top: -2rem;
			}
		}
	}
	.primary-nav__list {
		list-style: none;
		position: relative;
		height: 7rem;
		box-shadow: 780px 0 0 0px $white_translucent, -780px 0 0 0px $white_translucent;
		.primary-nav__interior {
			h3 {
				@include sans-uppercase-normal;
				// font-size: 1.8rem;
			}
		}
	}
	.primary-nav__item {
		padding: 0.8rem;
		background-color: $white_translucent;
		line-height: 1;
		height: 7rem;
		> a {
			color: $black_translucent;
			font-family: $sans;
			text-transform: uppercase;
			font-weight: bold;
			// font-size: 1.6rem;
			position: relative;
			&:hover {
				color: $nacac_link_hover;
			}
			&.has-children:after {
				font-family: $icons;
				//content: "\f078";
				display: inline-block;
				//width: 1em;
				position: absolute;
				//right: 1em;
			}
		}
		.interior__links a {
			display: block;
			margin: 1rem 0;
		}
	}
}



