@include break(max, $screen-sm-max) {
	.primary-nav__trigger {
		display: inline-block;
		position: fixed;
		@include zone-index(top, 1);
		top: 1rem;
		right: 1rem;
		padding: 0.8rem;
		transition: all 0.33s;
		color: $white;
		border: 1px solid;
	}
	.primary-nav {
		height: auto;
		// font-size: 1.7rem;
		background: rgba(255, 255, 255, 0.925);
		border-left: 1px solid #333;
		-webkit-transform: translateZ(0) translateX(100%);
		-ms-transform: translateZ(0) translateX(100%);
		transform: translateZ(0) translateX(100%);
		transition: all 0.33s cubic-bezier(0, 0.68, 0.44, 0.99);
		@include zone-index(overlay, 1);
		position: fixed;
		top: 8rem;
		right: 0;
		margin: 0;
		&.primary-nav--open {
			-webkit-transform: translateZ(0) translateX(0);
			-ms-transform: translateZ(0) translateX(0);
			transform: translateZ(0) translateX(0);
		}
		.container {
			padding-left: 0;
			padding-right: 0;
		}
		li {
			width: 100%;
			display: block;
		}
		.primary-nav__logo {
			display: none;
		}
		ul.primary-nav__list {
			box-shadow: none;
			height: calc(100vh - 7rem);
			width: 100vw;
			overflow: auto;
			overflow-x: hidden;
			overflow: auto;
			overflow-x: hidden;
			li.primary-nav__item {
				display: block;
				height: auto;
				overflow: hidden;
				background-color: transparent;
			}
		}
		.visible-menu-level {
			> li {
				margin-left: 0 !important;
			}
		}
		.primary-nav__interior {
			transition: all 0.33s cubic-bezier(0, 0.68, 0.44, 0.99);
			//height: 0;
			// nav ul li {
		}
	}
}


