@include break(min, $screen-md-min) {
	.main-nav .logo {
		margin-top: 2.6rem;
	}
	.selected {
		background: $white;
		color: purple;
	}
	.nav-header-buttons {
		display: none;
	}
	.primary-nav {
		position: relative;
		padding: 0;
		height: auto;
		width: 100%;
		overflow: visible;
		@include zone-index('page',1)
		transition: 400ms all ease-in-out;
		transform: translateY(55rem);
		// LETS GET WEIRD
		&.primary-nav--up{
			// bottom:55rem;
			transform: translateY(0);
			//top: 5rem;
		}
		&.primary-nav--down{
			// bottom:55rem;
			// transform: translateY(0);
		}
		&.primary-nav--sticky {
			position: fixed;
			top: 0;
			left: 0;
			overflow: visible;
			@include zone-index(page, 1);
			background: $nacac_nav_only_grey;
		}

		.primary-nav__interior {
			top: 7rem;
			bottom: inherit;
			@include prefix(transform-origin, top, webkit moz ms o);
		}
		.selected > .primary-nav__interior {
			@include prefix(transform, scale(1, 1), webkit moz ms o);
		}

		&:after {
			clear: both;
			content: "";
			display: table;
		}
		ul {
			position: static;
			height: auto;
			width: auto;
			background: transparent;
			overflow: visible;
			a {
				border-bottom: none;
				transition: .33s color 0.33s;
			}
		}
		>li {
			margin: 0;
			color: $white;
			padding: 0 .5em;
			text-align: center;
			display: inline-block;
			&.mobile-utility-bar-buttons {
				display: none;
			}
			>a {
				/* main navigation buttons style */
				position: relative;
				display: inline-block;
				padding: 1em;
				overflow: visible;
				border-bottom: none;
				color: $white;
				transition: color 0.3s, box-shadow 0.3s;
				&:hover {
					color: orange;
				}
				&.has-children {
					padding-right: 2.8rem;
					&:after {
						position: absolute;
						right: 0.5rem;
						font-family: Ionicons;
						content: "\f123";
					}
				}
				/*end a:first-child*/
			}
			/*end a*/
		}
		li.selected {
			border-bottom: 0.8rem solid $nacac_blue;
			padding-bottom: 0;
			@include prefix(transition, 300ms, webkit moz ms o);
			&.has-children:after {
				content: "\f12a";
			}
		}
		// .selected > .primary-nav__interior {
		// 	@include prefix(transform, scale(1, 1), webkit moz ms o);
		// }
		// &.primary-nav--up {
		// 	.primary-nav__interior {
		// 		top: inherit;
		// 		bottom: 7rem;
		// 		@include prefix(transform-origin, bottom, webkit moz ms o);
		// 	}
		// 	.selected > .primary-nav__interior {
		// 		@include prefix(transform, scale(1, 1), webkit moz ms o);
		// 	}
		// }
		// &.primary-nav--up, &.primary-nav--down {
		// }
		.primary-nav__interior {
			box-shadow: 780px 0 0 0px $white_translucent, -780px 0 0 0px $white_translucent;
			position: absolute;
			width: 100%;
			left: 0;
			background: $white_translucent;
			padding: 2em 0;
			text-align: left;
			color: #333;
			overflow: hidden;
			@include zone-index( 'base', 0);
			@include prefix(transform, scale(1, 0), webkit moz ms o);
			@include prefix(transition, 300ms, webkit moz ms o);
			&:after {
				clear: both;
				content: "";
				display: table;
			}
			> li {
				float: left;
				width: 25%;
				margin: 0;
				padding: 0 2em;
				min-height:4rem;
				h4{
					margin:0;
				}
				img {
					width: 100%;
				}
				p {
					width: auto;
					margin: 1rem 0;
					// font-size: 1.4rem;
				}
				&.go-back {
					display: none;
				}
				&.nav-article {
					float: right;
					width: 33%;
					.content-block-image-wrapper {
						margin-bottom: 1em;
					}
					p {
						margin-bottom: 0;
					}
				}
				/*nested*/
				ul.primary-nav__interior {
					position: relative;
					top: 0;
					max-width: 20rem;
					margin: 0;
					padding: 0;
					li {
						float: none;
						width: 100%;
						margin-top: 0;
						> ul {
							// font-size: 1.4rem;
						}
					}
					/*end li*/
				}
				/*end ul*/
			}
			/*end li*/
		}
		/*end nav interior*/
	}
	/*end primary-nav*/
}
